import { Component, OnInit } from '@angular/core';
import { CompanyService } from 'src/app/services/company.service';
import { Categories } from 'src/app/modules/category/category.module';
import { Coupon } from 'src/app/modules/coupon/coupon.module';

@Component({
  selector: 'app-addcoupon',
  templateUrl: './addcoupon.component.html',
  styleUrls: ['./addcoupon.component.css']
})
export class AddcouponComponent implements OnInit {
  public coupon: Coupon = new Coupon();
  public categories: Categories;
  constructor(private client: CompanyService) { }

  ngOnInit() {
  }

  public addNewCoupon() {
    this.client.addNewCoupon(this.coupon).subscribe(result => {
      alert("Coupon created with id: " + result.id + " and his name is: " + result.title);

    });
  }
}
