import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Company } from '../modules/company/company.module';
import { Observable } from 'rxjs';
import { Customer } from '../modules/customer/customer.module';
@Injectable({
  providedIn: 'root'
})
export class AdminService {

  constructor(private client: HttpClient) { }

  
  public addCompany(company:Company){
    return this.client.post<Company>("http://localhost:8080/admin/addcomp/" + sessionStorage.token, company,);
}
public updateCompany(company:Company){
  return this.client.put<Company>("http://localhost:8080/admin/updatecomp/" + sessionStorage.token , company );
  
}
public deleteCompany(id:number){
  return this.client.delete<Company>("http://localhost:8080/admin/deletecomp/"  + sessionStorage.token +"/"+ id );
}

public getAllCompanies(): Observable<Company[]>{
  return this.client.get<Company[]>("http://localhost:8080/admin/allcompanies/" + sessionStorage.token);
}
public getOneCompany(id:number){
  return this.client.get<Company>("http://localhost:8080/admin/getOneCompany/" + sessionStorage.token +"/" +id );
}
public addCustomer(customer:Customer){
  return this.client.post<Customer>("http://localhost:8080/admin/addcustomer/" + sessionStorage.token, customer );
}
public updateCustomer(customer:Customer){
  return this.client.put<Customer>("http://localhost:8080/admin/updatecustomer/" + sessionStorage.token, customer );
}
public deleteCustomer(id:number){
  return this.client.delete<Customer>("http://localhost:8080/admin/deletecustomer/" + sessionStorage.token + "/" + id);
}
public getAllCustomers(): Observable<Customer[]>{
  return this.client.get<Customer[]>("http://localhost:8080/admin/allcustomers/" + sessionStorage.token  );
}
public getOneCustomer(id:number){
  return this.client.get<Customer>("http://localhost:8080/admin/customer/" + sessionStorage.token+"/" +id );
}



}
