import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/modules/coupon/coupon.module';
import { CustomerService } from 'src/app/services/customer.service';

@Component({
  selector: 'app-customer-coupons-by-price',
  templateUrl: './customer-coupons-by-price.component.html',
  styleUrls: ['./customer-coupons-by-price.component.css']
})
export class CustomerCouponsByPriceComponent implements OnInit {
  public coupons: Coupon[] = new Array;
  public coupon: Coupon = new Coupon();
  public maxPrice: number;
  constructor(private client: CustomerService) { }


  ngOnInit() {
    this.client.getAllCoupons().subscribe((result) => {
      this.coupons = result;
    });
  }

  public getCustomerCouponsByPrice() {
    this.client.getCustomerCouponsByPrice(this.maxPrice).subscribe((result) => {
      this.coupons = result;
    });
  }

}
