import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/modules/coupon/coupon.module';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-updatecoupon',
  templateUrl: './updatecoupon.component.html',
  styleUrls: ['./updatecoupon.component.css']
})
export class UpdatecouponComponent implements OnInit {
  public coupon: Coupon = new Coupon();

  constructor(private client: CompanyService) { }

  ngOnInit() {
  }

  public updateCoupon(){
    this.client.updateCoupon(this.coupon).subscribe(result =>{
      alert("Coupon: " + result.id +" , " + result.title + " updated")

    });
  }


}
