import { Component, OnInit } from '@angular/core';
import { CompanyService } from 'src/app/services/company.service';
import { Coupon } from 'src/app/modules/coupon/coupon.module';

@Component({
  selector: 'app-couponsbyprce',
  templateUrl: './couponsbyprce.component.html',
  styleUrls: ['./couponsbyprce.component.css']
})
export class CouponsbyprceComponent implements OnInit {
  public coupons: Coupon[] = new Array;
  public coupon: Coupon = new Coupon();
  public maxPrice: number;
  constructor(private client:CompanyService) { }

  ngOnInit() {
  }

  public getCompanyCouponsByPrice() {
    this.client.getCompanyCouponsByPrice(this.maxPrice).subscribe((result) => {
      this.coupons = result;
    });
  }

}
