import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../modules/coupon/coupon.module';
import { Observable } from 'rxjs';
import { Company } from '../modules/company/company.module';

@Injectable({
  providedIn: 'root'
})
export class CompanyService {

  constructor(private client: HttpClient) { }


  public addNewCoupon(coupon: Coupon){
    return this.client.post<Coupon>("http://localhost:8080/company/addcoupon/" + sessionStorage.token , coupon);
  }

  public updateCoupon(coupon: Coupon){
    return this.client.put<Coupon>("http://localhost:8080/company/updatecoupon/" + sessionStorage.token , coupon);
  }

  public deleteCoupon(id: number){
    return this.client.delete<Coupon>("http://localhost:8080/company/deletecoupon/" + sessionStorage.token +"/" + id);
  }

  public getCompanyCoupons(): Observable<Coupon[]>{
    return this.client.get<Coupon[]>("http://localhost:8080/company/companyCoupons/" + sessionStorage.token);
  }

  public getCompanyCouponsByCategory(coupon: string){
    return this.client.get<Coupon[]>("http://localhost:8080/company/couponsbycategory/" + sessionStorage.token + "/category/" + coupon);

  }

  public getCompanyCouponsByPrice(maxPrice: number){
    return this.client.get<Coupon[]>("http://localhost:8080/company/couponsbyprice/" + sessionStorage.token + "/price/" + maxPrice);
  }

  public getCompanyDetails(){
    return this.client.get<Company>("http://localhost:8080/company/companydetails/" + sessionStorage.token);
  }









}
