import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/modules/coupon/coupon.module';
import { CustomerService } from 'src/app/services/customer.service';

@Component({
  selector: 'app-purchase-coupon',
  templateUrl: './purchase-coupon.component.html',
  styleUrls: ['./purchase-coupon.component.css']
})
export class PurchaseCouponComponent implements OnInit {

  public coupons: Coupon[] = new Array;
  public coupon: Coupon = new Coupon();
  public maxPrice: number;
  constructor(private client:CustomerService) { }

  ngOnInit() {
    this.client.getAllCoupons().subscribe((result) => {
      this.coupons = result;
    });
  } 

  public purchaseCoupon(coupon: Coupon) {
    this.client.purchaseCoupon(coupon).subscribe(result => {
      alert("Coupon purchased");
      coupon = result;

    })
  }

}
