import { Component, OnInit } from '@angular/core';
import { CustomerService } from 'src/app/services/customer.service';
import { Coupon } from 'src/app/modules/coupon/coupon.module';

@Component({
  selector: 'app-customer-coupons',
  templateUrl: './customer-coupons.component.html',
  styleUrls: ['./customer-coupons.component.css']
})
export class CustomerCouponsComponent implements OnInit {
  public coupons: Coupon[] = new Array;

  constructor(private client: CustomerService) { }

  ngOnInit() {
    this.client.getCustomerCoupons().subscribe(result=>{
      this.coupons = result;
    })
  }

}
