import { Component, OnInit } from '@angular/core';
import { CompanyService } from 'src/app/services/company.service';
import { Coupon } from 'src/app/modules/coupon/coupon.module';

@Component({
  selector: 'app-companycoupons',
  templateUrl: './companycoupons.component.html',
  styleUrls: ['./companycoupons.component.css']
})
export class CompanycouponsComponent implements OnInit {
  public coupons: Coupon[] = new Array;

  constructor(private client:CompanyService) { }

  ngOnInit() {
    this.client.getCompanyCoupons().subscribe((result) => {
      this.coupons = result;
    });
  }

}
