import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/modules/coupon/coupon.module';
import { CustomerService } from 'src/app/services/customer.service';

@Component({
  selector: 'app-delete-coupon-purchase',
  templateUrl: './delete-coupon-purchase.component.html',
  styleUrls: ['./delete-coupon-purchase.component.css']
})
export class DeleteCouponPurchaseComponent implements OnInit {

  public coupons: Coupon[] = new Array;
  public coupon: Coupon = new Coupon();

  constructor(private client: CustomerService) { }

  ngOnInit() {
    this.client.getCustomerCoupons().subscribe(result=>{
      this.coupons = result;
    })
  }

  public deleteCouponPurchase(coupon: Coupon){
    this.client.deleteCouponsPurchase(coupon).subscribe(result=>{
      alert("");
      coupon = result;
    })
  }

}
