import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../modules/coupon/coupon.module';
import { Customer } from '../modules/customer/customer.module';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  constructor(private client: HttpClient) { }


public purchaseCoupon(coupon: Coupon){
  return this.client.post<Coupon>("http://localhost:8080/customer/customerpurchase/" + sessionStorage.token , coupon);
}

public getAllCoupons(){
  return this.client.get<Coupon[]>("http://localhost:8080/customer/allcoupons/" + sessionStorage.token);
}

public getCustomerCoupons(){
  return this.client.get<Coupon[]>("http://localhost:8080/customer/customercoupons/" + sessionStorage.token);
}

public getCustomerCouponsByCategory(coupon: string){
  return this.client.get<Coupon[]>("http://localhost:8080/customer/customercouponsbycategory/" + sessionStorage.token + "/category/" + coupon);
}
public getCustomerCouponsByPrice(maxPrice: number){
  return this.client.get<Coupon[]>("http://localhost:8080/customer/customercouponsbyprice/" + sessionStorage.token + "/price/" + maxPrice);
}

public getCustomerDetails(){
  return this.client.get<Customer>("http://localhost:8080/customer/customerdetails/" + sessionStorage.token);
}

public deleteCouponsPurchase(coupon: Coupon){
return this.client.put<Coupon>("http://localhost:8080/customer/deletepurchasedcoupon/"+ sessionStorage.token , coupon);
}




}

